
































































































import { Component, Vue, Prop } from "vue-property-decorator";
import { getRoleList } from "../../../api/system/index2";
// import { Form as ElForm } from "element-ui";

@Component({
  components: {}
})
export default class CopyUsers extends Vue {
  constructor() {
    super();
  }
  @Prop({ default: null }) data: any;
  dialogVisible = true;
  checked1 = false;
  checked2 = false;

  selectList: any = [];

  cancel() {
    this.$emit("cancel", false);
  }

  finish() {
    if (this.currentItem.id) {
      this.$emit("finish", this.currentItem);
    } else {
      alert(`未选择数据`);
    }
  }
  currentItem: any = { id: null };
  list: any = [];
  total: any = null;
  currentPage = 1;
  handleSizeChange(a: any) {
    console.log(a);
  }
  handleCurrentChange(a: any) {
    console.log(a);
  }
  async search() {
    const { total, list } = await getRoleList();
    this.total = total;
    this.list = list;
  }
  async created() {
    this.search();
  }
  handleSelectionChange(val: any) {
    console.log(val);
    this.selectList = val;
  }
  currentChange(val: any) {
    console.log(val);
    this.currentItem = val;
  }
}
